@use "sass:math";
@use 'variables' as v;
@use 'mixins' as m;

.btn-contacto{
    @include m.boton(v.$asignar, block);

    @include m.tablet{
        margin: 0 auto math.div(v.$separacion, 2) auto;      
        width: 25%;        
    }
    &:hover{
        background-color: v.$verde;
    }
}