body {
  font-family: "Spartan", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Spartan", sans-serif;
  margin: 3.5rem 0;
  text-align: center;
  color: #070056;
}

h1 {
  font-weight: 900;
  font-size: 2.6rem;
}
