  h2 {
    font-size: 3rem;
  }
}
@media (min-width: 2200px) {
  h2 {
    font-size: 4.6rem;
  }
}

h3 {
  font-weight: 400;
  font-size: 2rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2.8rem;
  }
}
@media (min-width: 2200px) {
  h3 {
    font-size: 3.8rem;
  }
}

h4 {
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  h4 {
    font-size: 2.2rem;
  }
}
@media (min-width: 2200px) {
