  margin-bottom: 2.5rem;
}

.seccion:last-of-type {
  margin-bottom: 0;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.no-click {
  pointer-events: none;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode h1, .dark-mode h2, .dark-mode h2 {
  color: #6f9bbe;
}
.dark-mode img, .dark-mode picture {
  filter: brightness(0.9);
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode .dark-mode-boton::after {
  height: 0.3rem;
  width: 0.3rem;
  top: 34%;
  right: 34%;
}
@media (min-width: 2200px) {
  .dark-mode .dark-mode-boton::after {
    height: 0.6rem;
    width: 0.6rem;
  }
}
.dark-mode .barra {
  border-bottom: 0.1rem solid #FFFFFF;
}
@media (min-width: 768px) {
  .dark-mode .barra {
    border-bottom: none;
  }
}
.dark-mode .barra img {
  filter: invert(100%) brightness(1.5);
}
.dark-mode .navegacion a {
  color: #FFFFFF;
}
.dark-mode .mobile-menu .btnMenu {
  color: #FFFFFF;
}
.dark-mode .mobile-menu .btnMenu:hover {
  color: #5388B1;
}
.dark-mode .no-borde {
  border-bottom: none;
}
.dark-mode .mostrar {
  border-bottom: 0.1rem solid #FFFFFF;
  background-color: #1c1c1c;
}
.dark-mode .mostrar a:hover {
  color: #333333;
}
.dark-mode .footer {
  background-color: #030028;
}
.dark-mode .valor::after, .dark-mode .valor-corporativo::after {
  background-color: #5388B1;
}
.dark-mode .valor p, .dark-mode .valor-corporativo p {
  color: #6f9bbe;
}

.btn-contacto {
  background-color: #070056;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.btn-contacto:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .btn-contacto {
    margin: 0 auto 2.5rem auto;
    width: 25%;
  }
}
.btn-contacto:hover {
  background-color: #33A78B;
}

