  .main-content {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}
.main-content .enlaces-main {
  position: relative;
}
.main-content .enlaces-main .enlaces {
  display: none;
  width: 100%;
  padding: 1rem 0;
  bottom: 12%;
  padding: 2rem;
}
.main-content .enlaces-main .enlaces a {
  color: #FFFFFF;
  border-radius: 2rem;
  padding: 0.5rem 1.5rem;
  border: 0.25rem solid #FFFFFF;
  font-weight: 400;
  font-size: 1.4rem;
}
@media (min-width: 480px) {
  .main-content .enlaces-main .enlaces a {
    padding: 1rem 2rem;
