  .main-content .enlaces-main .enlaces a {
    padding: 1rem 3rem;
    font-size: 1.6rem;
    border: 0.3rem solid #FFFFFF;
  }
}
@media (min-width: 2200px) {
  .main-content .enlaces-main .enlaces a {
    padding: 2rem 5rem;
    font-size: 2.4rem;
    border: 0.5rem solid #FFFFFF;
  }
}
