.main-content .enlaces-main:hover .enlaces {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.main-content .enlaces-main:hover picture {
  mix-blend-mode: multiply;
}
.main-content .enlaces-main:hover img {
  mix-blend-mode: multiply;
}

