.light {
  filter: invert(65%);
}

.barra {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 1rem;
  align-items: center;
  position: relative;
  margin: 1rem auto;
  padding-bottom: 1rem;
  border-bottom: 0.1rem solid #070056;
}
.barra img {
  width: 7rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: none;
  }
  .barra img {
    width: 10rem;
  }
}
@media (min-width: 1024px) {
  .barra img {
    width: 12rem;
  }
}
@media (min-width: 2200px) {
  .barra {
    margin: 5rem 0;
  }
  .barra img {
    width: 20rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
  height: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.navegacion a {
  color: #070056;
  text-decoration: none;
  font-size: 1.4rem;
  display: block;
  text-align: center;
  background-color: rgba(83, 136, 177, 0.5);
  padding: 0.5rem 0;
  margin: 0.5rem;
  font-weight: 500;
}
.navegacion a:hover {
  background-color: #F2DB8F;
}
@media (min-width: 768px) {
  .navegacion a {
    background-color: transparent;
    font-size: 1.6rem;
    padding: 0;
    margin: 0;
    margin-right: 4.2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:hover {
    color: #5388B1;
    background-color: transparent;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    margin-right: 6.5rem;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    font-size: 1.8rem;
  }
}
@media (min-width: 2200px) {
  .navegacion a {
    font-size: 3.2rem;
    margin-right: 15rem;
  }
}
.navegacion .submenu {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  margin-left: 2.5rem;
  cursor: pointer;
}
.navegacion .dropdown {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
  height: 0;
  position: relative;
}
.navegacion .visible {
  position: absolute;
  visibility: visible;
  opacity: 1;
  height: auto;
  background-color: #383838;
}
.navegacion .visible a {
  text-align: center;
  padding: 0.2rem 2rem;
  margin: 0;
}
.navegacion .visible a:hover {
  background-color: #4a4a4a;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu .btnMenu {
  color: #070056;
  margin: 1rem 0;
  cursor: pointer;
}
.mobile-menu .btnMenu:hover {
  color: #5388B1;
}

.no-borde {
  border-bottom: none;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
  padding: 1rem;
  border-bottom: 0.1rem solid #070056;
  z-index: 11;
  background-color: #FFFFFF;
}

.active {
  color: #5388B1 !important;
}

.footer {
