  margin: auto;
  margin-top: 2.5rem;
  width: 10rem;
}
@media (min-width: 768px) {
  .footer-info .info img {
    margin: 0;
    margin-top: 2.5rem;
    width: 20rem;
  }
}
.footer-info .info .politicas {
  display: grid;
  place-items: center;
}
.footer-info .info .politicas p {
  text-align: center;
  margin: 1rem 0;
}
.footer-info .info .politicas p:hover {
  color: #5388B1;
}
@media (min-width: 768px) {
  .footer-info .info .politicas {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .footer-info .info .politicas p {
    text-align: right;
    margin: 1rem 0;
  }
  .footer-info .info .politicas p:hover {
    color: #5388B1;
  }
}

